import { Component, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';

import { IFormControlComponent } from '@helix/platform/shared/api';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { ICheckboxFormControlOptions } from './boolean-form-control-options.interface';

@Component({
  selector: 'rx-checkbox-form-control',
  templateUrl: './boolean-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: BooleanFormControlComponent,
      multi: true
    }
  ]
})
export class BooleanFormControlComponent extends ValueAccessor<boolean> implements IFormControlComponent {
  @Input()
  options: ICheckboxFormControlOptions;

  @Input()
  isDisabled: boolean;

  id: string;
}
